import axios from 'axios'

export const createCategory = async (category, authtoken) => {
  return await axios.post(
    `${process.env.REACT_APP_API}/createCategory`,
    category,
    {
      headers: {
        authtoken,
      },
    }
  )
}
export const getCategories = async () => {
  return await axios.get(`${process.env.REACT_APP_API}/listAllCategory`)
}

export const getCategory = async (slug) => {
  return await axios.get(
    `${process.env.REACT_APP_API}/readCategory/${slug}`,
    {}
  )
}
export const removeCategory = async (slug, authtoken) => {
  return await axios.delete(
    `${process.env.REACT_APP_API}/removeCategory/${slug}`,
    {
      headers: {
        authtoken,
      },
    }
  )
}
export const updateCategory = async (slug, category, authtoken) => {
  return await axios.put(
    `${process.env.REACT_APP_API}/updateCategory/${slug}`,
    category,
    {
      headers: {
        authtoken,
      },
    }
  )
}

export const getCategorySubs = async (_id) => {
  return await axios.get(
    `${process.env.REACT_APP_API}/category/subCategory/${_id}`
  )
}
